@use "base/variables" as v;
@use "base/mixins" as m;

.sobreMiHero {
  background-image: url("../img/sobreMiHero.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
}

.quienSoy__SM {
  padding-top: 1.5rem;
  color: v.$blanco;
}

.descripcion {
  background-color: rgba(v.$blanco, $alpha: 0.4);
  display: flex;
  justify-content: center;

  .parrafo__descripcion {
    color: v.$negro;
    font-size: 1.5rem;
    font-weight: 600;
    padding: 1rem 5rem 2rem;
    text-align: center;
  }
}

.titulo-cualidades {
  display: flex;
  align-items: center;
  justify-content: center;

  .titulo__cualidades {
    margin-right: 0.5rem;
    margin-bottom: 1.2rem;
  }
}

ul {
  list-style: url("/build/img/inciso.svg");

  li {
    margin-bottom: 2rem;
    font-weight: 100;
  }
}

//Conocimientos
.contenedor-Conocimientos {
  background-color: v.$blanco;

  .subti_conocimiento {
    text-align: center;
    margin: 3.5rem 0;
    text-transform: uppercase;
  }

  .p__conocimiento {
    font-weight: 400;
    font-size: 1.3rem;
  }
}

.tecnologias__contenedor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .contenedor-logo__conocimiento {
    justify-content: center;
    align-items: center;

    .logo {
      height: 8rem;
      width: 8rem;
      margin: 2rem 0;
    }
  }
}

@include m.tablet {
  .tecnologias__contenedor {
    display: flex;
    flex-direction: column;
    align-items: center;

    .contenedor-logo__conocimiento {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 3rem 8rem;
    }
  }
}
