@use "base/variables" as v;
@use "base/mixins" as m;

.header {
  height: min-content;
  width: 100%;
  background: v.$primarioTransparente;
  z-index: 10;

  @include m.tablet {
    &.fijo {
      left: 0;
      top: 0;
      position: fixed;
      height: 4rem;
      background: v.$primarioOpaco;

      .barra {
        margin-top: -1rem;
      }
    }
  }

  .header__texto {
    text-emphasis: center;
    color: v.$negro;
    margin-top: 3rem;
  }
}

.logo {
  font-family: v.$fuenteLogo;
  color: v.$blanco;

  .logo__nombre {
    font-weight: 400;
  }

  .logo__bold {
    font-weight: bold;
    color: v.$negro;
  }
}

.barra {
  padding-top: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;

  @include m.tablet {
    flex-direction: row;
    justify-content: space-between;
    text-align: center;
    align-items: center;
    padding-bottom: 1rem;
    gap: 2rem;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 0.5rem;

  a {
    color: v.$negro;
    font-size: 0.9rem;
    font-weight: 600;
  }

  @include m.tablet {
    flex-direction: row;
    gap: 1.2rem;
  }
}

.navegacion__enlace {
  transition: background-color 0.5s;
}

.navegacion__enlace:hover {
  background-color: v.$secundario;
}
