html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Heebo", sans-serif;
  font-size: 1, 6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 1rem;
  color: #000000;
  line-height: 1.5;
}

ul li {
  font-size: 1rem;
  color: #000000;
  line-height: 1.2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  font-family: "Rubik", sans-serif;
  line-height: 1.2;
}

h1 {
  font-size: 2.7rem;
}

h2 {
  font-size: 3.8rem;
}

h3 {
  font-size: 3.2rem;
}

h4 {
  font-size: 2.5rem;
}

p {
  font-family: "Quicksand", sans-serif;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.texto-borde {
  -webkit-text-stroke: 0.1rem #454545;
}

.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0;
}

.centrar-texto {
  text-align: center;
}

.upper {
  text-transform: uppercase;
}

.scrollTop {
  background-color: #fbfd54;
  border-radius: 50%;
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3rem;
  width: 3rem;
  position: fixed;
  bottom: 2rem;
  right: -5rem;
  opacity: 0;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}
.scrollTop svg {
  position: relative;
  top: -1.1rem;
  right: 1.45rem;
  transform: scale(0.7);
}
.scrollTop.active {
  animation: slide-in 0.5s forwards;
}
.scrollTop .hide {
  opacity: 0;
  pointer-events: none;
  animation: slide-out 0.5s forwards;
}
.scrollTop .hide.active {
  opacity: 1;
  pointer-events: auto;
  animation: slide-out 0.5s forwards;
}

@keyframes slide-in {
  from {
    right: -5rem;
    opacity: 0;
  }
  to {
    right: 4rem;
    opacity: 1;
  }
}
@keyframes slide-out {
  from {
    right: 4rem;
    opacity: 1;
  }
  to {
    right: 10rem;
    opacity: 0;
  }
}
.scrollTop:hover {
  transform: scale(1.1);
  border-color: rgba(0, 0, 0, 0.1);
}

.header {
  height: min-content;
  width: 100%;
  background: rgba(199, 206, 90, 0.4);
  z-index: 10;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    height: 4rem;
    background: rgb(233, 235, 189);
  }
  .header.fijo .barra {
    margin-top: -1rem;
  }
}
.header .header__texto {
  text-emphasis: center;
  color: #000000;
  margin-top: 3rem;
}

.logo {
  font-family: "Josefin Sans", sans-serif;
  color: #ffffff;
}
.logo .logo__nombre {
  font-weight: 400;
}
.logo .logo__bold {
  font-weight: bold;
  color: #000000;
}

.barra {
  padding-top: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    text-align: center;
    align-items: center;
    padding-bottom: 1rem;
    gap: 2rem;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 0.5rem;
}
.navegacion a {
  color: #000000;
  font-size: 0.9rem;
  font-weight: 600;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 1.2rem;
  }
}

.navegacion__enlace {
  transition: background-color 0.5s;
}

.navegacion__enlace:hover {
  background-color: #fbfd54;
}

.hero {
  background-image: url("../img/hero.webp");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
}
.hero .hero__encabezado {
  color: #ffffff;
  display: flex;
  justify-content: center;
  text-align: center;
}
.hero .hero__shadow {
  background-color: rgba(0, 0, 0, 0.6);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.hero .hero__ubicacion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5;
}
.hero p {
  color: #ffffff;
}

.contenedor-servicios {
  background-color: rgba(241, 225, 95, 0.15);
  height: 100%;
  width: 100%;
  padding: 2rem 4rem;
  border: 2rem 4rem;
}

.servicios__especif,
.tarjetas__formato {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.servicios__especif h3 {
  color: #1d1e0f;
  font-weight: normal;
}
.servicios__especif p {
  line-height: 2;
}
.servicios__especif .servicios__titulos {
  text-align: center;
  font-size: 2.5rem;
}
.servicios__especif .servicios__icono {
  height: 10rem;
  width: 10rem;
  background-color: #c6ab25;
  border-radius: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

@media (min-width: 768px) {
  .servicios,
  .tarjetas {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 0;
  }
  .servicios__icono {
    margin: 4rem;
    align-items: center;
  }
  .menosMargenBottom {
    margin-bottom: 0;
  }
  .recorteBordeInf {
    margin-bottom: 0.5rem;
  }
}
@media (min-width: 1200px) {
  .servicios,
  .tarjetas {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 1rem;
  }
  .servicios__icono {
    margin: 4rem;
    align-items: center;
  }
  .menosMargenBottom {
    margin-bottom: -0.5rem;
  }
  .recorteBordeInf {
    margin-bottom: 0.5rem;
  }
}
.servicios__info {
  text-align: center;
}

.servicios__icono_logo_react {
  width: 6rem;
  display: flex;
  animation-name: spin;
  animation-duration: 8s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  z-index: -1;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.footer {
  height: min-content;
  background: rgba(199, 206, 90, 0.4);
  display: flex;
}

.barra_footer {
  padding-top: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .barra_footer {
    flex-direction: row;
    justify-content: space-between;
    text-align: center;
    align-items: center;
    gap: 2rem;
  }
}

.sobreMiHero {
  background-image: url("../img/sobreMiHero.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
}

.quienSoy__SM {
  padding-top: 1.5rem;
  color: #ffffff;
}

.descripcion {
  background-color: rgba(255, 255, 255, 0.4);
  display: flex;
  justify-content: center;
}
.descripcion .parrafo__descripcion {
  color: #000000;
  font-size: 1.5rem;
  font-weight: 600;
  padding: 1rem 5rem 2rem;
  text-align: center;
}

.titulo-cualidades {
  display: flex;
  align-items: center;
  justify-content: center;
}
.titulo-cualidades .titulo__cualidades {
  margin-right: 0.5rem;
  margin-bottom: 1.2rem;
}

ul {
  list-style: url("/build/img/inciso.svg");
}
ul li {
  margin-bottom: 2rem;
  font-weight: 100;
}

.contenedor-Conocimientos {
  background-color: #ffffff;
}
.contenedor-Conocimientos .subti_conocimiento {
  text-align: center;
  margin: 3.5rem 0;
  text-transform: uppercase;
}
.contenedor-Conocimientos .p__conocimiento {
  font-weight: 400;
  font-size: 1.3rem;
}

.tecnologias__contenedor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.tecnologias__contenedor .contenedor-logo__conocimiento {
  justify-content: center;
  align-items: center;
}
.tecnologias__contenedor .contenedor-logo__conocimiento .logo {
  height: 8rem;
  width: 8rem;
  margin: 2rem 0;
}

@media (min-width: 768px) {
  .tecnologias__contenedor {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .tecnologias__contenedor .contenedor-logo__conocimiento {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem 8rem;
  }
}
.contenedor-proyectos .descripcion-proyectos {
  margin: 2rem 3rem;
}
.contenedor-proyectos .proyectos {
  margin: 5rem;
  display: grid;
}

.proyectos {
  display: flex;
  grid-column: auto;
  grid-auto-flow: column;
  gap: 0rem 2rem;
}
.proyectos .ssProyecto {
  height: 15rem;
  width: 40rem;
}

.contenedor-cargando {
  display: grid;
  place-content: center;
  height: 15rem;
}
.contenedor-cargando .cargando {
  width: 12rem;
  height: 3rem;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: space-between;
  margin-top: 0 auto;
}
.contenedor-cargando .cargando .text-cargando {
  font-weight: 800;
  padding-top: 2rem;
  font-size: 2rem;
  text-transform: uppercase;
}
.contenedor-cargando .cargando .ball {
  width: 3rem;
  height: 3rem;
  background-color: #00b8de;
  animation: salto 0.5s alternate infinite;
  border-radius: 50%;
}
.contenedor-cargando .cargando .ball:nth-child(2) {
  animation-delay: 0.18s;
}
.contenedor-cargando .cargando .ball:nth-child(3) {
  animation-delay: 0.37s;
}
@keyframes salto {
  from {
    transform: scaleX(1.25);
  }
  to {
    transform: translateY(-50px) scaleX(1);
  }
}