@use "base/variables" as v;
// Box Sizing
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: v.$fuentePrincipal;
  font-size: 1, 6rem;

  &.fijar-body {
    overflow: hidden;
  }
  &.body-scroll {
    padding-top: 8.6rem;
  }
}

p {
  font-size: 1rem;
  color: v.$negro;
  line-height: 1.5;
}

ul {
  li {
    font-size: 1rem;
    color: v.$negro;
    line-height: 1.2rem;
  }
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  font-family: v.$fuenteHeading;
  line-height: 1.2;
}

h1 {
  font-size: 2.7rem;
}
h2 {
  font-size: 3.8rem;
}
h3 {
  font-size: 3.2rem;
}
h4 {
  font-size: 2.5rem;
}
p {
  font-family: v.$fuenteSoporte;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.texto-borde {
  -webkit-text-stroke: 0.1rem v.$grisOscuro;
}

.no-margin {
  margin: 0;
}
.no-padding {
  padding: 0;
}
.centrar-texto {
  text-align: center;
}

.upper {
  text-transform: uppercase;
}

//Boton de scroll hacía arriba
.scrollTop {
  background-color: v.$secundario;
  border-radius: 50%;
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3rem;
  width: 3rem;
  position: fixed;
  bottom: 2rem;
  right: -5rem;
  opacity: 0;
  cursor: pointer;
  transition: all 0.5s ease-in-out;

  svg {
    position: relative;
    top: -1.1rem;
    right: 1.45rem;
    transform: scale(0.7);
  }

  &.active {
    animation: slide-in 0.5s forwards;
  }

  .hide {
    opacity: 0;
    pointer-events: none;
    animation: slide-out 0.5s forwards;
  }

  .hide.active {
    opacity: 1;
    pointer-events: auto;
    animation: slide-out 0.5s forwards;
  }
}

@keyframes slide-in {
  from {
    right: -5rem;
    opacity: 0;
  }
  to {
    right: 4rem;
    opacity: 1;
  }
}

@keyframes slide-out {
  from {
    right: 4rem;
    opacity: 1;
  }
  to {
    right: 10rem;
    opacity: 0;
  }
}

//Agranda botón al posicionar el cursor
.scrollTop:hover {
  transform: scale(1.1);
  border-color: rgba(0, 0, 0, 0.1);
}
