@use "base/variables" as v;
@use "base/mixins" as m;

.footer {
  height: min-content;
  background: v.$primarioTransparente;
  display: flex;
}

.barra_footer {
  padding-top: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;

  @include m.tablet {
    flex-direction: row;
    justify-content: space-between;
    text-align: center;
    align-items: center;
    gap: 2rem;
  }
}
