@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-proyectos {
  .descripcion-proyectos {
    margin: 2rem 3rem;
  }

  .proyectos {
    margin: 5rem;
    display: grid;
  }
}

//Estilos de proyectos
.proyectos {
  display: flex;
  grid-column: auto;
  grid-auto-flow: column;
  gap: 0rem 2rem;

  .ssProyecto {
    height: 15rem;
    width: 40rem;
  }
}

// Cargando con pelotitas
.contenedor-cargando {
  display: grid;
  place-content: center;
  height: 15rem;

  .cargando {
    width: 12rem;
    height: 3rem;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 0 auto;

    .text-cargando {
      font-weight: 800;
      padding-top: 2rem;
      font-size: 2rem;
      text-transform: uppercase;
    }

    .ball {
      width: 3rem;
      height: 3rem;
      background-color: v.$complementario;
      animation: salto 0.5s alternate infinite;
      border-radius: 50%;
    }

    .ball:nth-child(2) {
      animation-delay: 0.18s;
    }
    .ball:nth-child(3) {
      animation-delay: 0.37s;
    }

    @keyframes salto {
      from {
        transform: scaleX(1.25);
      }
      to {
        transform: translateY(-50px) scaleX(1);
      }
    }
  }
}
