@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-servicios {
  background-color: rgba(241, 225, 95, 0.15);
  height: 100%;
  width: 100%;
  padding: 2rem 4rem;
  border: 2rem 4rem;
}

.servicios__especif,
.tarjetas__formato {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.servicios__especif {
  h3 {
    color: v.$tituloAlterna;
    font-weight: normal;
  }

  p {
    line-height: 2;
  }

  .servicios__titulos {
    text-align: center;
    font-size: 2.5rem;
  }

  .servicios__icono {
    height: 10rem;
    width: 10rem;
    background-color: v.$primarioBlanco;
    border-radius: 50%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
}

@include m.tablet {
  .servicios,
  .tarjetas {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 0;
  }

  .servicios__icono {
    margin: 4rem;
    align-items: center;
  }

  .menosMargenBottom {
    margin-bottom: 0;
  }

  .recorteBordeInf {
    margin-bottom: 0.5rem;
  }
}

@include m.desktopXL {
  .servicios,
  .tarjetas {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 1rem;
  }

  .servicios__icono {
    margin: 4rem;
    align-items: center;
  }

  .menosMargenBottom {
    margin-bottom: -0.5rem;
  }

  .recorteBordeInf {
    margin-bottom: 0.5rem;
  }
}

.servicios__info {
  text-align: center;
}

.servicios__icono_logo_react {
  width: 6rem;
  display: flex;
  animation-name: spin;
  animation-duration: 8s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  z-index: -1;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
