@use "base/variables" as v;
@use "base/mixins" as m;

//HERO

.hero {
  background-image: url("../img/hero.webp");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;

  .hero__encabezado {
    color: v.$blanco;
    display: flex;
    justify-content: center;
    text-align: center;
  }

  .hero__shadow {
    background-color: rgba($color: #000000, $alpha: 0.6);
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .hero__ubicacion {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5;
  }

  p {
    color: v.$blanco;
  }
}
